<template>
  <div class="goodstype">
    <Card>
      <div class="title" slot="title">
        <div>商品分类</div>
        <el-button type="primary" @click="addTypeBtn" size="mini"
          >添加分类</el-button
        >
      </div>
      <div slot="content">
        <el-table :data="dataTable" style="width: 100%">
          <!-- 序号 -->
          <el-table-column type="index" label="序号" width="180">
          </el-table-column>
          <!-- 分类名称-->
          <el-table-column
            prop="dataTable.catename"
            label="分类名称"
            width="180"
          >
            <template slot-scope="scope">
              <span v-if="scope.row.isEdit">{{ scope.row.cateName }}</span>
              <el-input v-else v-model="scope.row.cateName"></el-input>
            </template>
          </el-table-column>
          <!-- 是否启用 -->
          <el-table-column label="是否启用">
            <template slot-scope="scope">
              <el-switch
                active-color="#13ce66"
                v-model="scope.row.state"
                :disabled="scope.row.isEdit"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                v-if="scope.row.isEdit"
                @click="edit(scope.row)"
                >编辑</el-button
              >
              <el-button
                size="mini"
                v-else
                type="success"
                @click="complate(scope.row)"
                >完成</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          :page-sizes="[3, 5, 10, 20]"
          layout="total, sizes, prev, pager, next, jumper"
        ></el-pagination>
      </div>
    </Card>
    <el-dialog title="添加分类" :visible.sync="dialogForm">
      <el-form :model="form">
        <el-form-item label="分类名称" label-width="200px">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" label-width="200px">
          <el-switch
            active-color="#13ce66"
            v-model="form.state"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm = false">取 消</el-button>
        <el-button type="primary" @click="ensure"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
//引入卡片插槽
import Card from "@/components/Card.vue";
//引入接口
import { api_getType, api_editType,api_addType,api_delType } from "@/api/goods";

export default {
  components: { Card },
  data() {
    return {
      currentPage: 1, //当前页面
      pageSize: 5, //当前条数
      total: 5, //全部条数
      dataTable: [], //表格数据
      dialogForm: false,
      form: { name: "", state: true, isEdit: "" },
    };
  },
  methods: {
    //获取列表渲染页面函数
    async getTypeList() {
      let { data, total } = await api_getType({
        currentPage: this.currentPage,
        pageSize: this.pageSize,
      });

      //遍历数组
      data.forEach((item) => {
        item.state = item.state == 1 ? true : false;
        item.isEdit = true;
      });
      //将遍历的数组返回给占位数组
      this.dataTable = data;
      //页数渲染
      this.total = total;
    },
    //单项编辑按钮
    edit(row) {
      row.isEdit = false;
    },
    //完成编辑
    async complate(row) {
      let { id, cateName, state } = row;

      let { code } = await api_editType({
        id: id,
        cateName: cateName,
        state: state,
      });
      if (code == 0) {
        row.isEdit = true;
      }
    },
    //单项删除按钮
  async  handleDelete(row) {
      let {code} = await api_delType({
        id:row.id
      })
      if(code==0){
        this.getTypeList()
      }
    },
    //当条数发生改变
    handleSizeChange(val) {
      this.pageSize = val;
      this.getTypeList();
    },
    //当页数发生改变
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getTypeList();
    },
    //对话弹窗按钮
    addTypeBtn() {
     this.dialogForm = true
    },
    //弹窗确定按钮
   async ensure(){
      let {code} =await api_addType({
        cateName:this.form.name,
        state:this.form.state
      })
      if(code==0){
        this.dialogForm = false
        this.getTypeList();
      }
    }
  },
  created() {
    this.getTypeList();
  },
};
</script>

<style lang="less" scoped>
.goodstype {
  background: #fff;
  border-radius: 4px;
  overflow: hidden;
  /deep/.title {
    display: flex;
    justify-content: space-between;
    height: 40px;
    align-items: center;
    border-bottom: 1px solid #eff1f7;
    padding: 10px;
  }
}
</style>